h1, h2, h3, h4, h5, h6, p, a, button, ul {
  margin: 0;
  padding: 0;
}

ul, li, a {
  color: inherit;
  line-height: 0;
  text-decoration: none;
  list-style: none;
}

button {
  all: unset;
  cursor: pointer;
  display: inline-block;
}

body {
  background-color: #ffebcd;
  font-family: Franklin Gothic Medium, Arial Narrow, Arial, sans-serif;
}

h1 {
  text-align: center;
  color: #915200;
  margin-top: 25px;
}

.backdrop {
  opacity: 1;
  background-color: #e3e0e0e6;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  padding-left: 120px;
  padding-right: 120px;
  transition-duration: .25s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden.backdrop {
  pointer-events: none;
  opacity: 0;
  transition-duration: .5s;
}

.modal-preview {
  background-color: #fff;
  border-radius: 25px;
  padding: 25px 25px 100px;
}

.backdrop.is-hidden .modal-preview {
  animation-name: modalDisappearance;
  animation-duration: .5s;
  animation-timing-function: ease-in-out;
}

.backdrop:not(.is-hidden) .modal-preview {
  animation-name: modalAppearance;
  animation-duration: .5s;
  animation-timing-function: ease-in-out;
}

@keyframes modalDisappearance {
  0% {
    transform: translate(0%)scale3d(1, 1, 1);
  }

  100% {
    transform: translate(150%, 100%)scale3d(.1, .1, 1);
  }
}

@keyframes modalAppearance {
  0% {
    transform: translate(-150%, -100%)scale3d(.1, .1, 1);
  }

  100% {
    transform: translate(0%)scale3d(1, 1, 1);
  }
}

